    transition-delay: 1.75s;
  }
  .media_columns_item.fs-checkpoint-active:nth-child(6) {
    transition-delay: 1.5s;
  }
  .media_columns_item.fs-checkpoint-active:nth-child(5) {
    transition-delay: 1.25s;
  }
  .media_columns_item.fs-checkpoint-active:nth-child(4) {
    transition-delay: 1s;
  }
  .media_columns_item.fs-checkpoint-active:nth-child(3) {
    transition-delay: 0.75s;
  }
  .media_columns_item.fs-checkpoint-active:nth-child(2) {
    transition-delay: 0.5s;
  }
  .media_columns_item.fs-checkpoint-active:nth-child(1) {
    transition-delay: 0.25s;
  }
  .media_columns_item:nth-child(odd) {
    padding-right: 2%;
  }
  .media_columns_item:nth-child(even) {
    padding-left: 2%;
  }
}
.media_link {
  position: relative;
  display: block;
  overflow: hidden;
}
.media_link:after {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0.3);
  content: '';
  opacity: 1;
  transition: background 0.3s linear;
}
.media_link:hover:after,
.media_link:focus:after {
  background: rgba(0, 0, 0, 0.6);
}
.media_icon {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  border-radius: 100%;
  transition: background 0.15s linear;
  transform: translate(-50%, -50%);
}
.media_image {
  display: block;
  transition: transform 0.3s ease;
}
a:hover .media_image,
a:focus .media_image {
  transform: scale(1.05);
}
